@import url("https://fonts.googleapis.com/css2?family=Tiro+Devanagari+Hindi:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Shadows+Into+Light:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Alice:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Sansita+One:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Trirong:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Tinos:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=DM+Serif+Display:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --sub-sub-heading: "Tiro Devanagari Hindi";
  --font-alice: Alice;
  --font-dm-serif-display: "DM Serif Display";
  --font-trirong: Trirong;
  --large-heading: "Sansita One";
  --font-tinos: Tinos;
  --font-shadows-into-light: "Shadows Into Light";

  /* font sizes */
  --font-size-5xl: 24px;
  --font-size-21xl: 40px;
  --sub-sub-heading-size: 9px;
  --font-size-sm: 14px;
  --font-size-xl-4: 20.4px;
  --large-heading-size: 28.4px;
  --font-size-mini: 15px;
  --font-size-2xs: 11px;
  --font-size-xl: 20px;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-gray-100: #8d8d8d;
  --color-mediumslateblue-100: #3354ff;
  --color-mediumslateblue-200: #0066ff;
  --color-dimgray: #6b6b6b;
  --color-limegreen: #17bd4f;
  --color-whitesmoke-100: #f6f6f6;
  --color-whitesmoke-200: #f5f5f5;

  /* Paddings */
  --padding-4xs-9: 8.9px;
  --padding-3xl-9: 22.9px;

  /* Border radiuses */
  --br-3xl-9: 22.9px;
  --br-sm: 14px;
  --br-xs: 12px;
}
